.content {
    margin-bottom: 16vmin;
}
.content header {
    display: flex;
    min-height: calc(100vh - 5.6vmax - 5ex);
    padding-top: 10vmin;
    padding-bottom: 10vmin;
    margin-bottom: 16vmin;
    flex-direction: column;
    justify-content: center;
    background: rgba(0,0,0,.02);
    padding-left: 24vw;
    padding-right: 24vw;
    margin-left: -24vw;
    margin-right: -24vw;
}
.content header h1 {
    font-size: 360%;
    font-weight: 700;
    letter-spacing: -.04em;
    margin-left: -2px;
    -webkit-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
    line-height: 1.1;
}
.content a {
    font-weight: 700;
    font-size:xx-large
}
.list {
    display: block;
    margin: 0 0 .4rem;
}
.list h1 {
    font-size: 360%;
    font-weight: 700;
    letter-spacing: -.04em;
    font-size: 200%;
    line-height: 1.1;
    opacity: 1;
    margin: 0;
}
