.current {
    color : rgb(218, 30, 171);
}


.nav {
    padding-top: 1.4vmax;
    padding-bottom: 1.4vmax;
    margin-right: -1vmax;
    margin-left: -1vmax;
    text-align: center;
    list-style: none;
    font-size: 60%;
    font-weight: 700;
    letter-spacing: .45em;
    text-transform: uppercase;
}

.nav li {
    display: inline-block;
}

.nav li a {
    padding: .5em 1vmax;
    padding-top: 0.5em;
    padding-right: 1vmax;
    padding-bottom: 0.5em;
    padding-left: 1vmax;
}

.nav li a:hover {
    color : rgb(218, 30, 171);
}


.content {
    font-family: var(--font-family-three);
}